@charset "UTF-8";

@import 'bootstrap';

.btn {
    box-shadow: 5px 5px 1px $dark;
}

.sw-navbar-dark-top {
    @extend .navbar-inverse;
    @extend .navbar-static-top;
    background-color: $dark;
    margin-bottom: 0;
    border-width: 0 0 0px;
    color: $light;
    height: 48px;
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 500;
    
    .sw-nav-logo {
        text-align: center;
        @extend .col-xs-4;
        @extend .col-xs-offset-4;
        position: relative;
        a {
            border-bottom-style: hidden;
            position: absolute;
            left: 0;
            right: 0;
        }
        img {
            height: 150px;
        }
    }
    
    .sw-dropdown-dark {
        background-color: $dark;
        a {
            color: $light;
        }
    }
    
    .sw-profile-icon {
        height: 30px;
        margin-top: -5px;
        margin-bottom: -5px;
        margin-right: 10px;
    }
    
    .sw-profile-header {
        height: 75px;
    }
}

.sw-navbar-lightdark-top {
    @extend .navbar-inverse;
    @extend .navbar-static-top;
    background-color: $lightdark;
    color: $light;
    z-index: 999;
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 50px;
}

.sw-navbar-transparent-top {
    @extend .navbar-default;
    @extend .navbar-static-top;
    background-color: transparent;
    border-width: 0 0 0px;
    color: $light;
    z-index: 999;
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 500;
}

.sw-nav {
    padding: 0;
    margin: 0;
    li {
        @extend .col-xs-2;
        text-align: center;
        list-style: none;
        line-height: 40px;
        margin-top: 10px;
        padding-left: 20px;
        padding-right: 20px;
        a {
            @extend .col-xs-12;
            border-bottom-width: 2px;
            border-bottom-style: solid;
        }
        
        a:link, a:visited, a:active, a:hover {
            color: $light;
            text-decoration: none;
            border-bottom-color: $light;
        }
        a:hover {
            color: $active;
            text-decoration: none;
            border-bottom-color: $active;
        }
    }
    li.active {
        a:link, a:visited, a:active, a:hover {
            color: $active;
            text-decoration: none;
            border-bottom-color: $active;
        }
    }
    li.sw-nav-logo-place-holder {
        @extend .col-xs-4;
    }
}

.sw-homepage-jumbotron {
    margin-top: -100px;
    width: 100%;
    height: 650px;
    z-index: -1000;
}

.sw-about-jumbotron {
    margin-top: -350px;
    width: 100%;
    height: 650px;
    z-index: -1000;
}

.sw-upload-jumbotron {
    position: absolute;
    top: 200px;
    left: 0;
    right: 0;
    margin-top: -100px;
    width: 100%;
    z-index: -1000;
}

.sw-homepage-content {
    position: relative;
    margin-top: -200px;
    background-color: $lightdark;
    z-index: 1;
    color: $light;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 40px;
    padding-right: 40px;
    
    .row {
        font-family: 'Georgia';
        font-size: 14px;
        border-color: $light;
        border-width: 1px;
        border-style: solid;
        padding-top: 10px;
        padding-bottom: 10px;
    }
}

.sw-upload-content {
    margin-top: 150px;
}

.sw-filter-panel {
    background-color: $lightdark;
    h3, h4, h5{
        color: $light;
    }
}

sw-gallery {
    a {
        position: relative;
    }
    img {
        switch: 100%;
    }
}

.sw-stars {
    margin-top: 30px;
}

h1, h2, h3, h4, h5 {
    font-family: 'Roboto';
}

h2 {
    color: $active;
}

footer {
    margin-top: 30px;
    margin-bottom: 10px;
}

html, body {
    min-height: 100%;
}

#map {
    position: absolute;
    top: 100px;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1000;
    background-color: green;
}